import {OrderModel} from "../../model/order";
import orderList from '../../components/orderList/orderList'
import noData from "../../components/noData/noData"
import popupLayer from '@/components/popup-layer/popup-layer.vue';
import choicePayType from '@/components/choice_pay_type/choice_pay_type.vue'
const orderModel = new  OrderModel()

export default {
    name: "order",
    data () {
        return {
            active:0,
            orders:[],
            status:true,
            orderData: {
                status: 0,
                page: 1,
                size: 10,
            },
            order_id:0,
            type:0
        }
    },
    components:{
        orderList,
        noData,
        choicePayType,
        popupLayer
    },
    methods:{
        lower(){
            if(!this.status) {
                return false
            }
            uni.showLoading({
                title:'loading...',
                icon: 'none'
            })
            let orderData = this.orderData
            this._getOrder(orderData)
        },
        swiperChange(e) {
            this.orders = []
            uni.showLoading({
                title:'loading...',
                icon: 'none'
            })
            this.active = e.detail.current;
            let orderData = this.orderData
            orderData.status = this.active
            orderData.page = 1
            this._getOrder(orderData)
        },
        _getOrder(orderData){
            orderModel.getOrderList(orderData).then((res) => {
                uni.hideLoading()
                let orders = this.orders
               if(res.code < 400 && res.data.length > 0) {
                   orders.push(...res.data)
                   let orderData = this.orderData
                   orderData.page = orderData.page + 1
                   this.orderData = orderData
                   this.orders = JSON.parse(JSON.stringify(orders))
                  
                   this.status =  true
               } else {
                   this.status =  false
               }
                 console.log(this.orders.length)
                console.log(this.orders)
            })
           
        },
        choicePayType(order_id){
            this.order_id = order_id
            this.$refs.popupRef.show()
        },
        closePayType(){
            this.$refs.popupRef.close()
        }
    },
     onLoad(option){
        this.type = option.type
       
    },
    onShow(){
        this.orders = []
        let orderData = this.orderData
        orderData.status = this.type
        orderData.page = 1
        this.active = this.type
        this._getOrder(orderData)
    }
}
